<template>
  <div class="justify-center w-full">
    <div class="flex justify-center">EDIT PIC</div>
    <div class="flex justify-center">
      <div class="text-red-600">{{ errorMessage }}</div>
      <div v-if="loadingPic">LOADING</div>
      <div v-if="loadingSuccess" class="w-96 h-96">
        <iframe
          src="/game2/index.html"
          width="100%"
          height="100%"
          scrolling="no"
          frameborder="0"
        >
        </iframe>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loadingPic: false,
      loadingSuccess: false,
      errorMessage: "",
    };
  },
  async mounted() {
    while (this.$store.state.waitingLogin) {
      console.log("login wait");
      await this.$store.wait(0.1);
    }

    const picUid = this.$route.params.pic_uid;
    console.log("EditPic", picUid);

    const reqData = {
      api: "getPicture",
      loginToken: localStorage.getItem("loginToken"),
      userUid: this.$store.getters.userUid,
      picUid: picUid,
    };

    console.log("getPicture req", reqData);
    this.loadingPic = true;
    const res = await this.$store.callAsync(reqData);
    this.loadingPic = false;

    if (res.code != 100) {
      this.errorMessage = res.message;
      return;
    }

    console.log("getPicture res", res);
    this.loadingSuccess = true;
    // this.$store.commit("setPicture", res.picture);
    const editPicData = {
      url: this.$store.getApiUrl(),
      loginToken: localStorage.getItem("loginToken"),
      userUid: this.$store.getters.userUid,
      pic: res.picture,
    };
    localStorage.setItem("editPicData", JSON.stringify(editPicData));
  },
};
</script>
